.SignUpForm {
    position: relative;

    &_DivForInput {
        position: relative;
        overflow: hidden;
        min-height: 55px;
        height: auto;
        width: 560px;
        margin-bottom: 5px;
        letter-spacing: -0.36rem;

        &__Input {
          letter-spacing: normal;
          height: 36px;
          font-size: 18px;
          outline: none;
          padding: 5px;
        }
    }
}
.LogInForm {
    position: relative;

    &_DivForInput {
        width: 560px;
        height: 48px;
        position: relative;
        overflow: hidden;
        margin-bottom: 5px;
        letter-spacing: -0.36rem;
        &.lightOnHover:hover {
            transition: all 0.3s ease;
            border: 4px lightblue solid;
            border-radius: 4px;
            &__Input {
                border-radius: 0;
            }
        }
        &__Input {
            letter-spacing: normal;
            height: 36px;
            padding: 5px;
            font-size: 18px;
            outline: none;
        }
    }
}

.FormToLogIn {
    flex-flow: column;
    position: relative;
    background-color: #e3f3ff;
    display: flex;
    align-items: center;
    margin-top: 10px;
    height: 500px;
    width: 1017px;
}

.Text {
    text-align: center;
    font-size: 24px;
    padding-top: 15px;
}

.DivSignUp {
    display: inline-block;
    font-size: 24px;
    width: 203px;
    position: relative;
    cursor: pointer;
    letter-spacing: normal;
}

.DivLogIn {
    display: inline-block;
    font-size: 24px;
    height: 32px;
    width: 68px;
    position: relative;
    cursor: pointer;
    letter-spacing: normal;
}

